<template>

	<component :is="as" v-bind="$attrs" v-on="$listeners">

		<slot></slot>

		<slot v-for="slot in slots" :name="slot" :slot="slot"/>

		<template v-for="slot in scopedSlots" :slot="slot" slot-scope="scope">
			<slot :name="slot" v-bind="scope"/>
		</template>

	</component>

</template>

<script>

	import { raf, relativeCoordsTo } from "../../js/util/dom";
	import { timeout } from "../../js/core";

	export default {

		name: "latte-ripple",

		props: {

			as: {
				default: "div",
				required: false,
				type: String
			},

			rippleBackground: {
				default: true,
				required: false,
				type: Boolean
			},

			rippleCentered: {
				default: false,
				required: false,
				type: Boolean
			},

			rippleDuration: {
				default: 180,
				required: false,
				type: Number
			},

			rippleOut: {
				default: false,
				required: false,
				type: Boolean
			}

		},

		data()
		{
			return {
				isBackgroundVisible: false,
				backgroundTimeout: null,
				container: null,
				preparedRipple: null
			};
		},

		destroyed()
		{
			this.container.remove();
		},

		mounted()
		{
			this.container = document.createElement("div");
			this.container.classList.add("ripple-container");

			if (this.rippleOut)
				this.container.classList.add("is-ripple-out");

			this.$el.prepend(this.container);

			this.$el.classList.add("is-ripple");

			if (window.TouchEvent)
			{
				this.$el.addEventListener("touchmove", evt => this.onPointerCancel(evt), {passive: true});
			}
			else
			{
				this.$el.addEventListener("pointercancel", evt => this.onPointerCancel(evt), {passive: true});
				this.$el.addEventListener("pointerout", evt => this.onPointerCancel(evt), {passive: true});
			}

			this.$el.addEventListener("pointerdown", evt => this.onPointerDown(evt), {passive: true});
			this.$el.addEventListener("pointerup", evt => this.onPointerUp(evt), {passive: true});
		},

		computed: {

			scopedSlots()
			{
				return Object.keys(this.$scopedSlots);
			},

			slots()
			{
				return Object.keys(this.$slots)
					.filter(k => k !== "default");
			}

		},

		methods: {

			createRipple(x, y)
			{
				const rect = this.$el.getBoundingClientRect();
				const size = Math.max(rect.width, rect.height);

				if (this.rippleCentered)
				{
					x = rect.width / 2;
					y = rect.height / 2;
				}

				const ripple = document.createElement("div");
				ripple.classList.add("ripple");
				ripple.style.setProperty("top", `calc(${y}px - (var(--ripple-size) / 2))`);
				ripple.style.setProperty("left", `calc(${x}px - (var(--ripple-size) / 2))`);
				ripple.style.setProperty("--ripple-duration", `${this.rippleDuration + 180}ms`);

				if (this.rippleOut)
				{
					ripple.style.setProperty("--ripple-scale", `${Math.max(size * .25, 12) / size}`);
					ripple.style.setProperty("--ripple-size", `${size * 1.25}px`);
				}
				else
				{
					ripple.style.setProperty("--ripple-scale", `${Math.max(size * .25, 12) / size}`);
					ripple.style.setProperty("--ripple-size", `${size * .75}px`);
				}

				this.container.appendChild(ripple);

				return ripple;
			},

			onPointerCancel()
			{
				if (this.preparedRipple !== null)
					this.preparedRipple.remove();

				this.isBackgroundVisible = false;
				this.preparedRipple = null;
			},

			onPointerDown(evt)
			{
				const {x, y} = relativeCoordsTo(this.$el, evt);

				this.isBackgroundVisible = true;
				this.preparedRipple = this.createRipple(x, y);
			},

			onPointerUp()
			{
				if (this.backgroundTimeout !== null)
					clearTimeout(this.backgroundTimeout);

				this.backgroundTimeout = timeout(this.rippleDuration, () =>
				{
					this.backgroundTimeout = null;
					this.isBackgroundVisible = false;
				});

				if (this.preparedRipple === null)
					return;

				const ripple = this.preparedRipple;
				this.preparedRipple = null;

				ripple.classList.add("is-scaling");
				ripple.classList.add("is-visible");

				raf(() => ripple.classList.add("is-hiding"), this.rippleDuration);
				raf(() => ripple.remove(), this.rippleDuration + 180);
			}

		},

		watch: {

			isBackgroundVisible()
			{
				if (this.isBackgroundVisible && this.rippleBackground && !this.rippleOut)
					this.container.classList.add("is-background-visible");
				else
					this.container.classList.remove("is-background-visible");
			},

			rippleOut()
			{
				if (this.rippleOut)
					this.container.classList.add("is-ripple-out");
				else
					this.container.classList.remove("is-ripple-out");
			}

		}

	}

</script>
